import React, { useEffect, useState } from 'react'
import { Button, Form, Container, Header } from 'semantic-ui-react'
import './App.css';
import { NotificationContainer, NotificationManager } from 'react-notifications';

export default function App() {

  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    car: '',
    job: '',
    caller: '',
    type: '',
    password: ''
  })
  const changeHandler = (e, type) => {
    setFormData({ ...formData, [typeof type !== 'undefined' ? type : [e.target.name]]: typeof type !== 'undefined' ? e.target.innerText : e.target.value })
  }

  const onSubmit = async (e) => {
    e.preventDefault();
    const data = new FormData();
    const copyData = {...formData}
    for (const iterator of Object.getOwnPropertyNames(copyData)) {
      data.append(iterator, copyData[iterator])
    }

    const sheetUrl = "https://script.google.com/macros/s/AKfycbz1IB_4tlUL9ayjqNo6AGrss_-HePbXWOXsXYUtfcqabhweoCL9Oa0Pyy3W5i-F1dYy/exec"
    try {
      const res = await fetch(sheetUrl, {
        method: 'POST',
        body: JSON.stringify(copyData),
        muteHttpExceptions: true,
        headers: {
          'Content-Type': "text/plain;charse=utf=8"
        }
      });

      const result = await res.json()
      console.log(result)
      
      setFormData({
        name: '',
        phone: '',
        car: '',
        job: '',
        caller: '',
        type: '',
        password: ''
      });

      if(result && result.result === 'success')
        NotificationManager.success('  ', '', 3000)
      else
        NotificationManager.error('    ', '', 3000)
    } catch (error) {
      console.log(error);
    }
  }
 
  useEffect(() => {
    console.log(formData)
  }, [formData])
  return (
    <div className='bg'>
      <Container fluid className="container">
        <Header as='h2' className='font'>  </Header>
        <Form className="form">
          <Form.Field>
            <label> </label>
            <input name='name' value={formData.name} onChange={changeHandler} placeholder='     ' />
          </Form.Field>
          <Form.Field>
            <label> </label>
          <input name='phone' value={formData.phone} onChange={changeHandler} placeholder='     ' />
          </Form.Field>
          <Form.Field>
            <label></label>
            <input name="car" value={formData.car} onChange={changeHandler} placeholder='   ' />
          </Form.Field>
          <Form.Field>
            <label> </label>
            <input name="job" value={formData.job} onChange={changeHandler} placeholder='      ' />
          </Form.Field>
          <Form.Field>
            <Form.Dropdown value={formData.caller} onChange={(e) => changeHandler(e, 'caller')} placeholder=" " fluid selection options={[
                { key: 1, text: " ", value:" " },
                { key: 2, text: " ", value:" " },
                { key: 3, text: " ", value: " " },
                { key: 4, text: " ", value: " " },
                { key: 5, text: " ", value: " " },
                { key: 6, text: " ", value: " " },
              ]}/>
          </Form.Field>
          <Form.Field>
            <Form.Dropdown value={formData.type} onChange={(e) => changeHandler(e, 'type')}  placeholder=" " fluid selection options={[
                  { key: 1, text: "", value:"" },
                  { key: 2, text: " ", value:" " },
                ]}/>
          </Form.Field>
          <Form.Field>
            <label> </label>
            <input type='password' name="password" value={formData.password} onChange={changeHandler} placeholder='    ' />
          </Form.Field>
          <Button className='font' onClick={onSubmit} color="blue" type='submit'> </Button>
        </Form>
      </Container>

      <NotificationContainer/>
    </div>
    )
}